@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;800&family=VT323&display=swap');

:root {
  --primary: #12bca2;
  --secondary: #6d15df;
}
h1,h2,h3 {
  margin: 0;
}

body {
  margin: 0;
  color: #333;
  font-family: 'Poppins';
  background: #f2f2f2;
}
nav {
  background: var(--primary);
  padding: 10px 20px;
  text-align: center;
}
nav a {
  color: #f2f2f2;
  margin: 10px;
  display: inline-block;
}
nav h1 {
  color: #fff;
}
.page {
  max-width: 1200px;
  margin: 20px auto;
  padding: 20px;
}

/* smoothie grid */
.smoothie-grid {
  margin-top: 40px; display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 40px;
  }


.smoothie-card {
  width: 100%;
  padding: 10px;
  background: #fff;
  box-sizing: border-box;
  border-radius: 6px; position: relative;
  }
  
  .smoothie-card .rating {
    position: absolute;
    top: -10px;
    right: -10px;
    background: var(--secondary);
    color: #fff;
    border-radius: 6px;
    width: 40px;
    height: 0;
    padding: 20px 0;
    line-height: 0px;
    Text-align: center;
    }
    


/* forms */
form {
    background: #fff;
    padding: 20px;
    max-width: 480px;
    margin: o auto;
    border-radius: 6px;
}

form input, form textarea {
  display: block;
  width: 100%;
  padding: 6px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  margin: 10px 0 20px 0;
  }
  

 form button {
    background: var(--primary);
    color: #fff;
    border: 0;
    border-radius: 6px;
    padding: 6px 8px;
    font-family: "Poppins";
    cursor: pointer;
 }


.smoothie-card .buttons {
  text-align: right;
}

 .smoothie-card i { color: #bbb;
  margin-left: 10px;
  font-size: 1. 2em;
  padding: 6px;
  background: #eee; 
  border-radius: 50%;
  cursor: pointer;
 }

 .order-by button { 
  margin-right: 10px;
  background: var(--primary);
  color: #fff;
  border: 0;
  padding: 4px 8px;
  border-radius: 6px; 
  font-family: "Poppins";
  cursor: pointer;
}